<template>
  <!--  -->
  <div class="main-wrapper">
    <!--  -->
    <div class="side-wrapper" >
      <div class="slide-wrapper-tow" style="width: 216px;left:0">
        <!--  -->
        <nav-bar style="padding-bottom: 80px">
          <Menu ref="rightMenu" width="100%" theme="dark" :accordion="false" @on-select="selectFn" :active-name="$route.path">
            <Menu-item   name="/query">
              <span class="queryMenuEllipsis">{{ $t('query') }}</span>
            </Menu-item>
          </Menu>
        </nav-bar>
        <!--  -->
      </div>
    </div>
    <!--  /-->
    <!--  -->
    <div class="main-container">
      <container>
        <div class="breadData">
          <!--  -->
          <div class="breadcrumb" >
            <Breadcrumb separator=">">
              <span>{{ $t('path') }}</span>
              <Breadcrumb-item >
                <!--<Icon type="ios-home-outline"></Icon>-->
                {{ $t('home') }}
              </Breadcrumb-item>
              <Breadcrumb-item >
                <!--<Icon :type="pound"></Icon>-->
                {{ $t('query') }}
              </Breadcrumb-item>
            </Breadcrumb>
          </div>
          <!--  /-->
          <div class="list breadList">
            <!--  v-show="state.searchState.show"-->
            <div class="searchData">
              <div class="listDataPath">
                <div class="pull-left">
                  <a>
                    <img :src="require('@/assets/img/u191.png')" alt="">
                    <span></span>
                  </a>
                </div>
              </div>
              <div class="listHead">
                <h4 class="pull-center">
                  
                </h4>
              </div>
              <Form :label-width="80" inline>
                <div class="clearfix xsAuto" style="padding:10px;text-align: center">
                  <Row class="listFormInput">
                    <Col span="12" >
                    <Row >
                      <Col span="24">
                      <Form-item label="">
                        <Checkbox size="large" v-model="params.category"></Checkbox>
                        <Select v-model="params.category" placeholder="" style="width:187px">
                          <Option value=""></Option>
                          <Option value=""></Option>
                        </Select>
                      </Form-item>
                      </Col>
                      <Col span="24">
                      <Form-item label="">
                        <Checkbox size="large" v-model="params.category"></Checkbox>
                        <Select v-model="params.category" placeholder="" style="width:187px">
                          <Option value=""></Option>
                          <Option value=""></Option>
                        </Select>
                      </Form-item>
                      </Col>
                      <Col span="24">
                      <Form-item label="">
                        <Checkbox  size="large" v-model="params.category"></Checkbox>
                        <Select v-model="params.category" placeholder="" style="width:187px">
                          <Option value=""></Option>
                          <Option value=""></Option>
                        </Select>
                      </Form-item>
                      </Col>
                    </Row>
                    </Col>
                    <Col span="10" >
                    <Button   size="large" style="margin-right: 30px;background: #e1e0de;margin-top: 60px">
                      <Icon type="stats-bars" style="vertical-align: bottom" size="20" ></Icon>
                      
                    </Button>
                    <Button  size="large" style="background: #e1e0de;margin-top: 60px">
                      <Icon type="android-list" style="vertical-align: bottom" size="20"></Icon>
                      
                    </Button>
                    </Col>
                  </Row>
                </div>
              </Form>
            </div>
            <!--  /-->
            <div class="listData">
              <Table :loading="loading2"   :size="tableSize"
                     :data="listData"
                     :border="true"
                     :columns="columns1" ref="table" @on-select="onSelect" @on-selection-change="onSelectionChange"></Table>
              <!--<Page :total="100" show-sizer show-elevator @on-change="pageChange" style="margin-top: 10px"-->
              <!--@on-page-size-change="PageSizeChange"></Page>-->
            </div>
          </div>
        </div>
      </container>
    </div>
    <!--  /-->
  </div>
  <!--  /-->
</template>
<script>
  import elementResizeDetectorMaker from 'element-resize-detector'
  var erd = elementResizeDetectorMaker()
  import NavBar from '@/components/layout/navbar/NavBar'
  import Container from '@/components/layout/container/Container'
  export default {
    name: 'list',
    components: {
      NavBar,
      Container
    },
    data () {
      return {
        formItem: {
          input: '',
          select: '',
          date: '',
          time: '',
          radio: '',
          checkbox: []
        },
        editModal: false,
        detailModal: false,
        deleteTip: false,
        loading2: false, // loading
        tableSize: 'default', // @:size
        DateReady: false, // 
        loading: false, // save
        currDate: {}, // 
        currIndex: 0, // 
        saveDisabled: false,
        params: {
          page: 1,
          limit: 10,
          category: 'Android'
        },
        selection: [], // 
        listData: [], // @:data
        columns1: [
          { // @:columns
            type: 'index', // 
            width: 60,
            align: 'center',
            title: ''
          },
          {
            title: '',
            key: 'who'
          },
          {
            title: '',
            key: 'who'
          },
          {
            title: '',
            key: 'type'
          },
          {
            title: '',
            key: 'type'
          },
          {
            title: '',
            key: 'desc',
            className: 'min-width'
          }
        ]
      }
    },
    watch: {
      /**
       * @params 
       * */
      params: {
        handler (val) {
          this.getData(val)
        },
        deep: true
      }
    },
    computed: {
      state () {
        return this.$store.state.app
      }
    },
    methods: {
      selectFn (a) {
        this.$router.push({
          path: a
        })
      },
      searchShow () {
        this.searchState = !this.searchState
      },
      /**
       * 
       * */
      refresh () {
        this.getData(this.params)
      },
      /**
       * @params:category  page limit:
       * */
      getData (params) {
        this.loading2 = false
        this.listData = []
//        this.$api.orderList(params).then((res) => {
//          if (!res.error) {
//            this.listData = res.results
//            this.listData = []
//            this.DateReady = true
//            this.loading2 = false
//          } else {
//            this.$Message.error(res.msg)
//            this.loading2 = false
//          }
//        })
      },
      /**
       * @on-change 
       * */
      pageChange (page) {
        this.params.page = page
      },
      /**
       * @on-page-size-change 
       * */
      PageSizeChange (limit) {
        this.params.limit = limit
      },
      /**
       * 
       * @show 
       * @remove 
       * @edit
       */
      show (index) {
        this.currIndex = index
        this.currDate = this.listData[index]
        this.detailModal = true
        this.$Modal.info({
          title: '',
          content: `${this.listData[index].who}<br>${this.listData[index].type}<br>${this.listData[index].desc}`
        })
      },
      remove (index) {
        this.listData.splice(index, 1)
      },
      edit (index) {
        this.editModal = true
        this.currIndex = index
        if (index === -1) { // 
          this.currDate = {
            createdAt: '',
            desc: '',
            publishedAt: '',
            type: '',
            used: true,
            who: '',
            url: 'c.fwone.com'
          }
        } else { // 
          this.currDate = this.listData[index]
        }
      },
      /**
       * 
       */
      deleteBatch () {
        this.deleteTip = false
        // ...
      },
      saveBatch () {
        this.loading = true
        setTimeout(() => {
          this.loading = false
          this.$Message.info('')
          this.editModal = false
        }, 3000)
      },
      /**
       * 
       * @ type 1  2
       */
      exportData (type) {
        if (type === 1) {
          this.$refs.table.exportCsv({
            filename: ''
          })
        } else if (type === 2) {
          this.$refs.table.exportCsv({
            filename: '',
            original: false
          })
        }
      },
      /**
       * 
       * selection row
       */
      onSelect (selection, row) {
        // console.log(selection,row)
      },
      onSelectionChange (selection) {
        this.selection = selection
      }
    },
    created () {
      this.getData(this.params)
    },
    mounted () {
      erd.listenTo(window, 'resize', this.handleResize)
    }
  }
</script>
<style lang="less" scoped>
  .breadData .breadList .searchData .listFormInput {
    text-align: left;
    margin: 0 0 0 60px;
  }
</style>



